import {getRequest} from '@/utils/api.js'
import {postRequest} from '@/utils/api.js'
import {deleteRequest} from '@/utils/api.js'
import {putRequest} from '@/utils/api.js'
export default {
data(){
  return {
    jl:{
      name:'',
      titleLevel:''
    },
    updateJl:{
      name:'',
      titleLevel:'',
      enabled:false
    },
    titleLevels:['正高级','中级','副高级','初级','员级'],
    jls:[],
    dialogVisible:false,
    multipleSelection:[]
  }
},
methods:{
  initJls(){
    console.log('initJls');
    getRequest('/system/basic/joblevel/').then(resp=>{
      if(resp){
        this.jls = resp
      }else{
        this.$message.error("请输入字段")
      }
    })
  },
  addJobLevel(){
    if(this.jl.name && this.jl.titleLevel){
      postRequest('/system/basic/joblevel/',this.jl).then(resp=>{
        if(resp){
          this.initJls()
          this.jl.name = ''
          this.jl.titleLevel = ''
        }
      })
    }
  },
  handleDelete(val){
    this.$confirm('是否删除'+val.name+'职称', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteRequest('/system/basic/joblevel/'+val.id).then(resp=>{
            if(resp){
              this.initJls();
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

  },
  showEditView(data){
    Object.assign(this.updateJl,data)
    this.updateJl.createDate = ''
    this.dialogVisible = true
  },
  doUpdate(){
    console.log(this.updateJl);
    putRequest('/system/basic/joblevel/',this.updateJl).then(resp=>{
        if(resp){
        this.initJls();
        }
    })
    this.dialogVisible = false
    //this.$router.go(0)
  },
  handleSelectionChange(val){
    this.multipleSelection = val
  },
  deleteMany(){
    this.$confirm('是否批量删除'+this.multipleSelection.length+'条职称', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let array = '?'
          this.multipleSelection.forEach(item=>{
          array+='ids='+item.id+'&'
         })
          deleteRequest('/system/basic/joblevel'+array).then(resp=>{
          if(resp){
            this.initJls();
           }
         })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
  }
},
mounted(){
  this.initJls()
},
updated(){
  //this.initJls()
}
}
