import {getRequest} from '@/utils/api.js'
import {postRequest} from '@/utils/api.js'
import {deleteRequest} from '@/utils/api.js'
import {putRequest} from '@/utils/api.js'
export default {
data(){
  return {
    pos:{
      name:''
    },
    positions:[

    ],
    dialogVisible:false,
    updatePos:{
      name:''
    },
    multipleSelection:[]
  }
},
methods:{
  initPositions(){
    getRequest('/system/config/pos/').then(resp=>{
      if(resp){
        this.positions = resp
      }
    })
  },
  handleEdit(index,data){
    Object.assign(this.updatePos,data)
    this.updatePos.createDate = ''
    console.log(this.updatePos);
    this.dialogVisible = true
  },
  handleDelete(index,data){
      this.$confirm('是否删除'+data.name, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteRequest('/system/config/pos/'+data.id).then(resp=>{
            if(resp){
              this.initPositions()
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

  },
  addPosition(){
    if(this.pos.name){
      postRequest('/system/config/pos/',this.pos).then(resp=>{
        if(resp){
          this.initPositions()
          this.pos.name=''
        }
      })
    }else{
      this.$message.error('请输入职位名称')
    }
  },
  doUpdate(){
    this.updatePos.creatDate = ''
    putRequest('/system/config/pos',this.updatePos).then(resp=>{
      if(resp){
        this.initPositions()
        this.dialogVisible = false
      }
    })
  },
  handleSelectionChange(val){
    this.multipleSelection = val
  },
  deleteMany(){
    this.$confirm('是否批量删除'+this.multipleSelection.length+'条职位', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let ids = '?'
          this.multipleSelection.forEach(item=>{
            ids+='ids='+item.id+'&'
          })
          deleteRequest('/system/config/pos/'+ids).then(resp=>{
            if(resp){
              this.initPositions()
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
  }
},
mounted(){
  this.initPositions()
}
}
