import {getRequest} from '@/utils/api.js'
import {postRequest} from '@/utils/api.js'
import {deleteRequest} from '@/utils/api.js'
import {putRequest} from '@/utils/api.js'
export default {
data(){
  return {
    role:{
      name:'',
      nameZh:''
    },
    roles:[],
    allMenus:[],
    defaultProps: {
          children: 'children',
          label: 'name'
        },
    selectMenus:[],
    activeName:-1
  }
},
methods:{
  initRoles(){
    getRequest('/system/basic/permiss/').then(resp=>{
      if(resp){
        this.roles = resp
      }
    })
  },
  initSelectMenus(rid){
    getRequest('/system/basic/permiss/mid/'+rid).then(resp=>{
      if(resp){
        this.selectMenus = resp
      }
    })
  },
  change(rid){
    if(rid){
      this.initMenus()
      this.initSelectMenus(rid)
    }
  },
  initMenus(){
    getRequest('/system/basic/permiss/menus/').then(resp=>{
      if(resp){
        this.allMenus = resp
      }
    })
  },
  doUpdate(id,index){
    let tree = this.$refs.tree[index]
    //tree.setCheckedKeys();
    let selectTrees = tree.getCheckedKeys(true);
    console.log("selectTrees:");
    console.log(selectTrees);
    let url = '/system/basic/permiss/?rid='+id
    selectTrees.forEach(element => {
      url+='&mids='+element;
    });
    putRequest(url).then(resp=>{
      if(resp){
        this.activeName = -1
      }
    })
  },
  cancelUpdate(){
    this.activeName = -1;
  },
  doAddRole(){
    if(this.role.name && this.role.nameZh){
      postRequest('/system/basic/permiss/role/',this.role).then(resp=>{
        if(resp){
          this.role.name = ''
          this.role.nameZh = ''
          this.initRoles()
        }
      })
    }else{
      this.$message.error('请输入字段')
    }
  },
  deleteRole(r){
    this.$confirm('是否删除'+r.name+'角色', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteRequest('/system/basic/permiss/'+r.id).then(resp=>{
            if(resp){
              this.initRoles();
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

  }
},
mounted(){
  this.initRoles()
},



}
