import {getRequest} from '@/utils/api.js'
import {postRequest} from '@/utils/api.js'
import {deleteRequest} from '@/utils/api.js'
import {putRequest} from '@/utils/api.js'
export default {
  name: "SysAdmin",
  data(){
    return {
      admins:[],
      keywords:'',
      allRoles:[],
      selectRoles:[]
    }
  },
  methods:{
    showPop(admin){
      this.initAllRoles();
      let roles = admin.roles;
      this.selectRoles = []
      roles.forEach(element => {
        this.selectRoles.push(element.id)
      });
    },
    initAllRoles(){
      getRequest('/system/admin/roles').then(resp=>{
        if(resp){
          this.allRoles=resp;
        }
      })
    },
    initAdmins(){
      getRequest('/system/admin/?keywords='+this.keywords).then(resp=>{
        if(resp){
          this.admins = resp
        }
      })
    },
    doSearch(){
      this.initAdmins()
    },
    deleteAdmin(val){
      this.$confirm('是否删除'+val.name+'操作员', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteRequest('/system/admin/'+val.id).then(resp=>{
            if(resp){
              this.initAdmins();
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });

    },
    enabledChange(admin){
      putRequest('/system/admin/',admin).then(resp=>{
        if(resp){
          this.initAdmins()
        }
      })
    },
    hidePop(admin){
      let flag = false;
      let roles = []
      Object.assign(roles,admin.roles)
      if(roles.length!=this.selectRoles.length){
        flag = true
      }else{
        for(let i=0;i<roles.length;i++){
          let role = roles[i]
          if(this.selectRoles.indexOf(role.id) == -1){
            flag = true
          }
        }
      }

      if(flag){
      let url = '/system/admin/role?adminId='+admin.id;
      this.selectRoles.forEach(item=>{
        url+='&rids='+item
      })
      putRequest(url).then(resp=>{
        if(resp){
          this.initAdmins()
        }
      })
    }
    }
  },
  mounted(){
    this.initAdmins()
  }
}
