import {getRequest} from '@/utils/api.js'
import {postRequest} from '@/utils/api.js'
import {deleteRequest} from '@/utils/api.js'
import {putRequest} from '@/utils/api.js'
export default {
watch: {
  filterText(val) {
  this.$refs.tree.filter(val);
  }
},
data(){
  return {
    filterText:'',
    deps:[],
    defaultProps: {
      children: 'children',
      label: 'name'
    },
    dialogVisible:false,
    dep:{
      name:'',
      parentId:-1
    },
    pname:''
  }
},
methods:{
  filterNode(value, data) {
    if (!value) return true;
    return data.name.indexOf(value) !== -1;
  },
  initDeps(){
    getRequest('/system/basic/department/').then(resp=>{
      if(resp){
        this.deps = resp;
      }
    })
  },
  showAddDep(data){
    this.pname = data.name
    this.dep.parentId = data.id
    this.dialogVisible=true
  },
  deleteDep(data){
    deleteRequest('/system/basic/department/'+data.id).then(
      resp=>{
        if(resp){
          this.initDeps()
        }
      }
    )
  },
  doAdd(){
    postRequest('/system/basic/department',this.dep).then(resp=>{
      if(resp){
        this.initDeps()
        this.dialogVisible = false
        this.dep.name=''
        this.dep.parentId=-1
        this.pname=''
      }
    })
  }
},
mounted(){
  this.initDeps()
}
}
